@import "../../scss/constants/responsive.scss";

.courses {
    width: 100vw;
    padding-bottom: 100px;
    height: auto;
    min-height: 80vh;
    padding-top: 80px;
    background-color: white;
    //background-color: #ecedee;
    //background-color: #1C1D1F !important;
    border-width: 0px !important;

    .courses-header {
        margin-bottom: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        letter-spacing: 0.5px !important;
        font-size: 30px;
    }

    .top-container {
        margin-top: 20px;
        min-height: 100%;
        display: flex !important;
        justify-content: center;
        align-items: center;

        .courses {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            flex-direction: row !important;

            &::-webkit-scrollbar {
                display: none;
            }


        }
    }
}